import axios from 'axios'

const SERVER = 'https://crud-react-klaw125.c9users.io'

class AuthorStore{
  constructor(ee){
    this.content = []
    this.ee = ee
  }
  getAll(){
    axios('/authors')
      .then((response) => {
        this.content = response.data
        this.ee.emit('AUTHOR_LOAD')
      })
      .catch((error) => console.warn(error))
  }
  createOne(author){
    axios.post('/authors', author)
      .then(() => this.getAll())
      .catch((error) => console.warn(error))
  }

  deleteOne(id){}
  saveOne(id, author){}
}

export default AuthorStore


